@import '../app/variables.scss';

.statement {
  width: 350px;
  display: flex;
  justify-content: space-between;
  border: 1px solid $gray;
  padding-left: 20px;
  padding-right: 20px;
  cursor: pointer;

  &__code {
    text-align: left;
    color: $purple;
    font-weight: 600;
  }

  &__unread {
    width: 25px;
    min-height: 25px;
    background-color: $red;
    border-radius: 50%;

    animation-delay: 1s;
    animation-name: grow-shrink;
    animation-duration: 1s;

    @keyframes grow-shrink {
      40% {
        transform: scale(1.5);
      }

      80% {
        transform: scale(1);
      }
    }
  }
}