.main {
  width: 800px;
  display: flex;
  justify-content: space-between;
  padding-top: 50px;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  min-height: calc(100vh - 170px - 170px);
  box-sizing: border-box;

  &__list {
    width: 400px;
    margin: 0;
    box-sizing: border-box;
    padding-left: 0;
  }
}
