@import '../app/variables.scss';

.section {
  width: 280px;
  display: flex;
  justify-content: space-between;
  border: 1px solid $gray;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: 30px;
  cursor: pointer;

  &__name {
    width: 235px;
    word-wrap: break-word;
    word-break: break-all;
    text-align: left;
    color: $purple;
    font-weight: 600;
  }

  &__unread {
    width: 25px;
    height: 25px;
    background-color: $red;
    border-radius: 50%;

    animation-delay: 0.5s;
    animation-name: grow-shrink;
    animation-duration: 1s;

    @keyframes grow-shrink {
      40% {
        transform: scale(1.5);
      }

      80% {
        transform: scale(1);
      }
    }
  }
}
